import React from 'react';
import { withRouteData } from 'react-static';
import { Link } from '@reach/router';
import styled from 'styled-components';

const Name = styled.h1`
  font-family: 'Montserrat', sans-serif;
  font-size: 3em;
  display: block;
  background: #83c0fd7d;
  padding: 10px;
  text-align: center;
  margin: 0;
  color: white;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.9)),
    url(https://images.pexels.com/photos/239898/pexels-photo-239898.jpeg?auto=compress&cs=tinysrgb&h=650&w=940)
      no-repeat;
`;

const Intro = styled.h2`
  font-size: 1.5em;
  font-family: 'Roboto', sans-serif;
  margin: 0.7em;
`;

const BlogList = styled.ul`
  list-style: none;
  padding-left: 0;
  margin: 0.7em;
  li {
    margin-bottom: 15px;
    padding: 5px
    border: 1px solid grey;
    a {
      text-decoration: none;
      color: black;
      display: inline-block;
      width: 100%;
    }
  }
`;

const BlogSummary = styled.div`
  width: 100%;
  // height: 5em;
  overflow: hidden;
`;

const BlogTitle = styled.h3`
  margin-top: 0.3em;
  margin-bottom: 0.3em;
`;
export default withRouteData(({ posts }) => (
  <div>
    <Name>Blog</Name>
    <Intro>Welcome to the External Techs blog!</Intro>
    <BlogList>
      {posts.map(post => (
        <li key={post.id}>
          <Link to={`/blog/post/${post.id}/`}>
            <BlogSummary>
              <BlogTitle>{post.title}</BlogTitle>
            </BlogSummary>
          </Link>
        </li>
      ))}
    </BlogList>
  </div>
));
